html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

/*<<--------Se agregan para este proyecto-------->>*/
h2:not(:first-child):nth-child(even) {
  border-bottom: 2px solid #333;
  padding: 0.5rem 2rem;
  margin: 3rem;
}

h2 {
  margin-top: 10px;
}

h2:target {
  scroll-margin-top: 10rem;
}

html {
  scroll-behavior: smooth;
}

.layout {
  width: 1366px;
  height: 768px;
  display: grid;
  grid: "header" auto "main" 1fr "footer" auto/1fr;
  gap: 8px;
}

.header {
  grid-area: header;
}

.main {
  grid-area: main;
}

.footer {
  grid-area: footer;
}

header {
  background-color: rgba(60, 121, 245, 0.5);
  width: 100%;
  z-index: 1000;
  top: 0;
  position: fixed;
  height: 65px;
}
@media (max-width: 480px) {
  header {
    background: none;
  }
}

.abrir-menu, .cerrar-menu {
  display: none;
  border: 0;
  font-size: 2rem;
}

@media (max-width: 480px) {
  header {
    height: 65px;
  }
  .abrir-menu {
    display: block;
  }
}
.navegador {
  display: flex;
  justify-content: end;
}

.navegador li {
  list-style-type: none;
  padding: 2rem 3rem;
  font-size: 2rem;
}

.navegador li a {
  color: #FFFFFF;
}

@media (max-width: 480px) {
  .navegador {
    opacity: 0;
    visibility: hidden;
    background: #3C79F5;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    height: 100vh;
  }
  .navegador.visible {
    opacity: 1;
    visibility: visible;
  }
  .cerrar-menu {
    display: block;
  }
}
.portada {
  content: url(../img/portada.jpg);
  width: 100vw;
  height: 50vh;
  margin-top: 65px;
}
@media (max-width: 480px) {
  .portada {
    margin-top: 0;
  }
}

.portada:target {
  scroll-margin-top: 9.5rem;
}

.perfil {
  display: flex;
}
@media (max-width: 480px) {
  .perfil {
    display: grid;
  }
}

.foto {
  display: grid;
  place-items: center;
}

.foto img {
  border-radius: 100%;
  width: 20rem;
  height: 20rem;
  margin: -10rem 2rem;
}

.skill {
  display: flex;
  justify-content: center;
}

#skill1 {
  content: url(../img/js.png);
  width: 5rem;
  height: 5rem;
}

#skill2 {
  content: url(../img/css.avif);
  width: 5rem;
  height: 5rem;
}

#skill3 {
  content: url(../img/html.avif);
  width: 5rem;
  height: 5rem;
}

#skill4 {
  content: url(../img/ts.avif);
  width: 5rem;
  height: 5rem;
}

#skill5 {
  content: url(../img/sass.avif);
  width: 5rem;
  height: 5rem;
}

#skill6 {
  content: url(../img/sql.avif);
  width: 5rem;
  height: 5rem;
}

.btn-like {
  width: 15rem;
  height: 5rem;
  margin-top: 10rem;
  background: rgba(60, 121, 245, 0.5);
  border-radius: 10%;
}

.educacion {
  display: flex;
  justify-content: center;
}
@media (max-width: 480px) {
  .educacion {
    display: grid;
    grid-template-columns: repeat(minmax(45rem, 1fr));
    gap: 20px;
    padding: 20px;
  }
}

.educacion section {
  border: 2px solid #E5E0FF;
  border-radius: 2%;
  margin: 2rem;
  padding: 0 3rem;
  width: 25vw;
}
@media (max-width: 480px) {
  .educacion section {
    width: 100%;
    margin: 0;
  }
}

.educacion section picture {
  width: 20rem;
  height: 20rem;
}

.educacion section img {
  width: 20vw;
  height: 30vh;
  padding-top: 2rem;
}
@media (max-width: 480px) {
  .educacion section img {
    width: auto;
  }
}

.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(45rem, 1fr));
  gap: 20px;
  padding: 20px;
}
@media (max-width: 480px) {
  .container {
    grid-template-columns: minmax(45rem, 1fr);
    padding: 0;
  }
}

.card {
  display: flex;
  flex-direction: column;
  height: 50rem;
  margin: 3rem;
  background-color: rgba(60, 121, 245, 0.5);
  padding: 3rem;
  border-radius: 10%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  transition: transform 0.3s ease;
}
@media (max-width: 480px) {
  .card {
    height: auto;
    width: 80%;
    margin: 0 auto;
    margin: 0rem;
  }
}

.card:hover {
  animation: shake 0.5s ease;
}

.button {
  text-align: center;
}

.card button {
  height: 5rem;
  width: 20rem;
  background: #3C79F5;
  color: #FFFFFF;
  border-radius: 1rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.formulario {
  display: flex;
  justify-content: center;
}

form {
  background: rgba(60, 121, 245, 0.5);
  width: 50rem;
  padding: 3rem;
  color: #FFFFFF;
  border-radius: 2%;
}
@media (max-width: 480px) {
  form {
    width: 80%;
    margin: 0 auto;
  }
}

.campo {
  display: flex;
  justify-content: space-between; /* Centrar horizontalmente */
  margin: 2rem;
}
@media (max-width: 480px) {
  .campo {
    display: grid;
    margin: 0 auto;
  }
}

.campo input {
  width: 30rem;
}
@media (max-width: 480px) {
  .campo input {
    width: auto;
  }
}

.campo textarea {
  width: 30rem;
  resize: none;
}
@media (max-width: 480px) {
  .campo textarea {
    width: 120%;
  }
}

.campo:last-of-type {
  justify-content: center;
  margin: 3rem 0 0 0;
}

.boton--primario {
  height: 4rem;
  pointer-events: none;
}

footer {
  background: rgba(60, 121, 245, 0.5);
  padding: 2rem 0;
  margin-top: 2rem;
}

footer p {
  margin: 0;
}

.footer {
  display: flex;
  justify-content: space-between;
  margin: 0 2rem;
}
@media (max-width: 480px) {
  .footer {
    margin: 0;
  }
}